<template>
	<div>
		<div class="grid grid-cols-1">
			<ul class="flex mx-5" :class="!main && 'justify-self-center'">
				<li class="py-2 px-1" :class="main ? 'w-full' : 'w-fit'" v-for="title in tabTitles" :key="title"
					@click="selectedTitle = title">
					<button class="w-full p-2 rounded-lg tracking-wider animate-default"
						:class="(selectedTitle == title) ?
						'bg-indigo-600 bg-opacity-100 text-indigo-100 font-bold shadow translate-y scale-y-105 animate-fast' :
						'bg-white bg-opacity-60 font-light text-opacity-90 shadow hover:font-normal hover:bg-opacity-75 hover:translate-y-0.5'">
						{{ title }}
					</button>
				</li>
			</ul>
		</div>
		<slot />
	</div>
</template>

<!---------------------------------------------------------------->

<script>
import { ref, provide } from 'vue'

export default {
	name: 'TabsWrapper',

	props: {
		main: {
			type: Boolean,
			default: false,
			required: false
		}
	},

	setup(props, { slots }) {

		const tabTitles = ref(slots.default().map((tab) => tab.props.title))
		const selectedTitle = ref(tabTitles.value[0])

		provide("selectedTitle", selectedTitle)

		return {
			selectedTitle,
			tabTitles,
		}
	}
}
</script>
