    const model = ref([])

    /**
     * returns a configuration object from a string describing its location and label
     * @param {String} string 
     */
    function findConfigByName(string) {
      const arr = string.split(".")

      var obj = { ...configs }

      for (var j = 0; j < arr.length; j++) {
        try {
          obj = obj[arr[j]]
        } catch (error) {
          console.warn('path error', error)
          return undefined
        }
      }

      return obj
    }

    /**
     * returns a configuration object from a string describing its location and label
     * @param {String} string 
     * @param {Object} configs 
     */
    function findLocalConfigByName(string, localConfigs) {
      const arr = string.split(".")

      var obj = localConfigs

      for (var j = 0; j < arr.length; j++) {
        try {
          obj = obj[arr[j]]
        } catch (error) {
          console.warn('path error', error)
          return undefined
        }
      }

      return obj
    }

    /**
     * 
     * @param {String} requirements
     */
    function dependency(requirements) {
      var isMet = true
      var label = ''
      var to_be = true

      for (var i = 0; i < requirements.length; i++) {
        var requirement = findConfigByName(requirements[i].label)

        label = requirements[i].label.split('.')
        label = label[label.length]
        to_be = requirements[i].to_be

        if (requirement.value !== requirements[i].to_be)
          isMet = false
      }

      return ({ label: label, value: isMet, to_be: to_be })
    }

    function localDependency(requirements, localConfigs) {
      var isMet = true
      var label = ''
      var to_be = true

      for (var i = 0; i < requirements.length; i++) {
        var requirement = findLocalConfigByName(requirements[i].label, localConfigs)

        label = requirements[i].label.split('.')
        label = label[label.length]
        to_be = requirements[i].to_be

        if (requirement.value !== requirements[i].to_be)
          isMet = false
      }

      return ({ label: label, value: isMet, to_be: to_be })
    }

    provide('configs', configs)
    provide('retailer select', computed(() => configs['retailer select']))
    provide('preset values', computed(() => configs['preset values']))
    provide('values online', computed(() => configs['values online']))
    provide('values in-store', computed(() => configs['values in-store']))
    provide('model', model)
    provide('find config by name', findConfigByName)
    provide('find local config by name', findLocalConfigByName)
    provide('dependency', dependency)
