<template>
  <div class="bg-indigo-100 rounded-2xl p-2 h-full">
    <div class="grid grid-cols-1">
      <div class="justify-self-center relative gap-2">
        <tabs-wrapper class="animate-default">
          <tab-component title="Table">
            <result-decoration>
              <table-vue />
            </result-decoration>
          </tab-component>
          <tab-component title="Bar chart">
            <result-decoration>
              <bar-chart />
            </result-decoration>
          </tab-component>
          <tab-component title="Radar chart">
            <result-decoration>
              <radar-chart />
            </result-decoration>
          </tab-component>
        </tabs-wrapper>
      </div>
    </div>
  </div>
</template >

<!---------------------------------------------------------------->

<script>
import TableVue from '@/components/visualizers/results/TableVue'
import BarChart from '@/components/visualizers/results/BarVue'
// import DoughnutChart from '@/components/visualizers/results/DoughnutChart'
import RadarChart from '@/components/visualizers/results/RadarVue'
import TabComponent from '@/components/containers/TabComponent'
import TabsWrapper from '@/components/containers/TabsWrapper'
import ResultDecoration from './ResultDecoration.vue'

import { inject, reactive, computed, provide } from 'vue'

export default {
  name: 'ResultsVue',
  components: {
    TableVue,
    BarChart,
    // DoughnutChart,
    RadarChart,
    TabComponent,
    TabsWrapper,
    ResultDecoration
  },

  props: {
    online: Object,
    instore: Object,
    onlineAlt: {
      type: Object,
      required: false,
      default: () => { return {} }
    },
    instoreAlt: {
      type: Object,
      required: false,
      default: () => { return {} }
    }
  },

  setup(props) {
    /**
     * IMPORTS
     */
    const retailerSelect = inject('retailer select')

    const onlineInstoreSelect = reactive({
      type: 'online',
      options: ['online', 'in-store']
    })

    const onlineData = computed(() => props.online)
    const instoreData = computed(() => props.instore)
    const onlineDataAlt = computed(() => props.onlineAlt)
    const instoreDataAlt = computed(() => props.instoreAlt)

    const retailerType = computed(() => retailerSelect.value.type !== 'online and in-store' ? retailerSelect.value.type : onlineInstoreSelect.type)

    provide('online/in-store select', onlineInstoreSelect)
    provide('retailer type', retailerType)
    provide('online data', onlineData)
    provide('in-store data', instoreData)
    provide('online data alt', onlineDataAlt)
    provide('in-store data alt', instoreDataAlt)
  }
}
</script>