import { watch } from 'fs'
import { watch } from 'fs'
<template>
  <!-- Various global.configs -->
  <form
    class="flex flex-col h-full overflow-y-auto scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100"
    v-on:submit.prevent>
    <section-wrapper :title="name">
      <div class="flex flex-col space-y-6 py-4">

        <!--Interventions Dropdown-->
        <dropdown title="Interventions">
          <ul class="space-y-2 w-full">
            <li v-for="(config, label) in interventions" :key="config">
              <checkbox :config="config" :label="label" v-model="config.value" :local="policies" />
            </li>
          </ul>
        </dropdown>

        <!--Return Policies and Requirements Dropdown-->
        <dropdown title="Return Policies and Requirements">
          <ul class="space-y-2 w-full">
            <li v-for="(config, label) in returnPoliciesAndRequirements" :key="config">
              <checkbox :config="config" :label="label" v-model="config.value" :local="policies" />
            </li>
          </ul>
        </dropdown>

        <!--Customer Convenience Dropdown-->
        <dropdown title="Customer Convenience">
          <ul class="space-y-2 w-full">
            <li v-for="(config, label) in customerConvenience" :key="config">
              <checkbox :config="config" :label="label" v-model="config.value" :local="policies" />
            </li>
          </ul>
        </dropdown>


        <!--List of Selected Policies-->
        <div class="flex flex-col rounded-md p-2 bg-indigo-100">
          <div class="flex flex-row">
            <span class="font-medium pb-2">selected Policies :</span>
            <hr class="grow" />
            <copy-button />
            <download-button />
          </div>

          <div class="clutter-box p-2">
            <!--If Policies are Selected-->
            <ul v-if="Object.keys(selectedPolicies).length > 0">
              <li v-for="(config, label) in selectedPolicies" :key="config">
                <checkbox :config="config" :label="label" v-model="config.value" :local="policies" />
              </li>
              <button @click="deselectAll"
                class="flex flex-row items-center p-4 pb-0 text-indigo-700 hover:text-indigo-400 animate-fast">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24"
                  stroke="currentColor" stroke-width="2">
                  <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                </svg>
                Clear all
              </button>
            </ul>
            <!--If No Policy is Selected-->
            <div v-else>
              None selected
            </div>
          </div>
        </div>
      </div>
    </section-wrapper>

    <div class="grow"></div>


    <!-- End buttons -->
    <div class="sticky bottom-0 animate-default border-slate-200 border-t-2"
      :class="state.open ? 'max-h-[100%]' : 'max-h-[0px]'">
      <div class="flex flex-row relative z-20 bg-white w-full items-end animate-default overflow-visible">
        <div class="absolute top-[-25px] grid place-items-center w-full">
          <button @click="toggle(state)"
            class="h-[25px] shadow-sm rounded-t-xl z-30 px-2 w-fit mr-0 animate-default origin-bottom"
            :class="state.open ? 'bg-indigo-600 text-indigo-100 shadow-md scale-y-[-100%]' : 'shadow-xs hover:shadow-md bg-slate-200 text-slate-600 hover:bg-indigo-200 hover:text-slate-800'">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 scale-y-50 rotate-180" fill="none"
              viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
              <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
            </svg>
          </button>
        </div>
        <div
          class="w-full overflow-x-scroll scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100">
          <results v-show="state.render" :online="valuesOnline" :instore="valuesInstore" class="mt-2 mx-2" />
        </div>
      </div>
    </div>
  </form>
</template>

<!---------------------------------------------------------------->

<script>
import Dropdown from '@/components/containers/Dropdown'
import Checkbox from '@/components/inputs/Checkbox'
import SectionWrapper from '@/components/containers/SectionWrapper'
import Results from '@/components/Results'
import CopyButton from './miscellaneous/CopyButton'
import DownloadButton from './miscellaneous/DownloadButton'

import { inject, reactive, computed } from 'vue'

export default {
  name: 'PoliciesPartition',
  components: {
    Dropdown,
    Checkbox,
    SectionWrapper,
    Results,
    CopyButton,
    DownloadButton
  },

  props: {
    side: String,
    modelValue: Object,
    name: String
  },

  setup(props, { emit }) {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')
    const configs = inject('configs')
    const model = inject('model')
    const policies = inject(props.side)
    const objectFilter = inject('object filter')
    const objectMap = inject('object map')

    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })

    const state = reactive({
      open: true,
      render: true,
    })

    const interventions = computed(() => policies['interventions'])
    const returnPoliciesAndRequirements = computed(() => policies['return policies and requirements'])
    const customerConvenience = computed(() => policies['customer convenience'])

    const selectedPolicies = computed(() => {
      var list = {}
      for (var i in policies) {
        list = { ...list, ...objectFilter(policies[i], (policy) => policy.value) }
      }

      return list
    })

    const activePolicies = computed(() => {
      var policies = []

      for (var i in selectedPolicies.value)
        policies = policies.concat(model.value.filter((policy) => policy.Factors === i))

      return policies
    })

    function applyPolicies(col, policies) {
      var i = 100

      for (var j in policies) {
        let x = 100 + parseFloat(policies[j][col])
        if (!isNaN(x))
          i *= x / 100
      }

      return i / 100
    }

    function onlineModel(data, policies) {
      const init = 'initial'
      const post = 'post policies'
      const obj = {
        'total sales': {},
        'duplicate sales': {},
        'total returns': {},
        'genuine sales': {},
        'fraudulent returns': {},
        'genuine returns': {},
        'wardrobing': {},
        'price arbitrage': {},
        'payment refund fraud': {},
        'multi-channel refund fraud': {},
        'shipping fraud': {},
        'sales - returns': {}
      }

      obj['total sales'][init] = data.main['total sales'].value
      obj['duplicate sales'][init] = data.main['duplicate sales rate'].value / 100 * obj['total sales'][init]
      obj['total returns'][init] = data.main['total returns rate'].value / 100 * obj['total sales'][init]
      obj['genuine sales'][init] = obj['total sales'][init] - obj['duplicate sales'][init]
      obj['fraudulent returns'][init] = data.main['fraudulent returns rate'].value / 100 * obj['total returns'][init]
      obj['genuine returns'][init] = obj['total returns'][init] - obj['fraudulent returns'][init]
      obj['wardrobing'][init] = data.fraud['wardrobing'].value / 100 * obj['fraudulent returns'][init]
      obj['price arbitrage'][init] = data.fraud['price arbitrage'].value / 100 * obj['fraudulent returns'][init]
      obj['payment refund fraud'][init] = data.fraud['payment refund fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['multi-channel refund fraud'][init] = data.fraud['multi-channel refund fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['shipping fraud'][init] = data.fraud['shipping fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['sales - returns'][init] = obj['total sales'][init] - obj['total returns'][init]

      obj['genuine sales'][post] = obj['genuine sales'][init] * applyPolicies('Normal online sales', policies)
      obj['duplicate sales'][post] = obj['duplicate sales'][init] * applyPolicies('Duplicate online sales', policies)
      obj['genuine returns'][post] = obj['genuine returns'][init] * applyPolicies('Purchased online returns', policies)
      obj['wardrobing'][post] = obj['wardrobing'][init] * applyPolicies('Wardrobing', policies)
      obj['price arbitrage'][post] = obj['price arbitrage'][init] * applyPolicies('Price arbitrage', policies)
      obj['payment refund fraud'][post] = obj['payment refund fraud'][init] * applyPolicies('Payment refund fraud', policies)
      obj['multi-channel refund fraud'][post] = obj['multi-channel refund fraud'][init] * applyPolicies('Multi-channel refund fraud', policies)
      obj['shipping fraud'][post] = obj['shipping fraud'][init] * applyPolicies('Shipping fraud', policies)
      obj['total sales'][post] = obj['genuine sales'][post] + obj['duplicate sales'][post]
      obj['fraudulent returns'][post] = obj['wardrobing'][post] + obj['price arbitrage'][post] + obj['payment refund fraud'][post] + obj['multi-channel refund fraud'][post] + obj['shipping fraud'][post]
      obj['total returns'][post] = obj['fraudulent returns'][post] + obj['genuine returns'][post]
      obj['sales - returns'][post] = obj['total sales'][post] - obj['total returns'][post]

      return obj
    }

    function instoreModel(data, policies) {
      const init = 'initial'
      const post = 'post policies'
      const obj = {
        'total sales': {},
        'total returns': {},
        'fraudulent returns': {},
        'wardrobing': {},
        'non-receipt shoplifted fraud': {},
        'receipt switching fraud': {},
        'receipt fraud': {},
        'payment refund fraud': {},
        'employee returns fraud': {},
        'multi-channel refund fraud': {},
        'genuine returns': {},
        'sales - returns': {}
      }

      obj['total sales'][init] = data.main['total sales'].value
      obj['total returns'][init] = data.main['total returns rate'].value / 100 * obj['total sales'][init]
      obj['fraudulent returns'][init] = data.main['fraudulent returns rate'].value / 100 * obj['total returns'][init]
      obj['wardrobing'][init] = data.fraud['wardrobing'].value / 100 * obj['fraudulent returns'][init]
      obj['non-receipt shoplifted fraud'][init] = data.fraud['non-receipt shoplifted fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['receipt switching fraud'][init] = data.fraud['receipt switching fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['receipt fraud'][init] = data.fraud['receipt fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['payment refund fraud'][init] = data.fraud['payment refund fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['employee returns fraud'][init] = data.fraud['employee returns fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['multi-channel refund fraud'][init] = data.fraud['multi-channel refund fraud'].value / 100 * obj['fraudulent returns'][init]
      obj['genuine returns'][init] = obj['total returns'][init] - obj['fraudulent returns'][init]
      obj['sales - returns'][init] = obj['total sales'][init] - obj['total returns'][init]

      obj['total sales'][post] = obj['total sales'][init] * applyPolicies('In-store sales', policies)
      obj['genuine returns'][post] = obj['genuine returns'][init] * applyPolicies('Purchased in-store returns', policies)
      obj['wardrobing'][post] = obj['wardrobing'][init] * applyPolicies('Wardrobing', policies)
      obj['non-receipt shoplifted fraud'][post] = obj['non-receipt shoplifted fraud'][init] * applyPolicies('Non-receipt shoplifted fraud', policies)
      obj['receipt switching fraud'][post] = obj['receipt switching fraud'][init] * applyPolicies('Receipt switching fraud', policies)
      obj['receipt fraud'][post] = obj['receipt fraud'][init] * applyPolicies('Receipt fraud', policies)
      obj['payment refund fraud'][post] = obj['payment refund fraud'][init] * applyPolicies('Payment refund fraud', policies)
      obj['employee returns fraud'][post] = obj['employee returns fraud'][init] * applyPolicies('Employee returns fraud', policies)
      obj['multi-channel refund fraud'][post] = obj['multi-channel refund fraud'][init] * applyPolicies('Multi-channel refund fraud', policies)
      obj['fraudulent returns'][post] = obj['wardrobing'][post] + obj['non-receipt shoplifted fraud'][post] + obj['receipt switching fraud'][post] + obj['receipt fraud'][post] + obj['payment refund fraud'][post] + obj['employee returns fraud'][post] + obj['multi-channel refund fraud'][post]
      obj['total returns'][post] = obj['fraudulent returns'][post] + obj['genuine returns'][post]
      obj['sales - returns'][post] = obj['total sales'][post] - obj['total returns'][post]

      return obj
    }

    function deselectAll() {
      objectMap(selectedPolicies.value, (config) => config.value = false)
    }

    const valuesOnline = computed(() => onlineModel(configs['values online'], activePolicies.value))
    const valuesInstore = computed(() => instoreModel(configs['values in-store'], activePolicies.value))

    data.value = reactive({
      'online': valuesOnline,
      'in-store': valuesInstore
    })

    return {
      state,
      toggle,
      policies,
      interventions,
      returnPoliciesAndRequirements,
      customerConvenience,
      selectedPolicies,
      deselectAll,
      valuesOnline,
      valuesInstore,
    }
  }
}
</script>