<template>
  <!-- Table + filters -->
  <div class="relative flex flex-row space-x-2 h-full overflow-y-hidden">
    <sidebar title="Filters">
      <ul>
        <li v-for="(obj, section) in filters" :key="section">
          <filter-dropdown :title="section" :filters="obj" />
        </li>
      </ul>
    </sidebar>
    <form class="overflow-auto scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100"
      @input="validate">
      <table-component :fields="fields" :data="model" />
    </form>
  </div>

  <!-- End buttons -->
  <div class="relative bg-white bottom-0 border-t-2 pt-2">
    <div class="flex flex-row items-center">
      <!--Download CSV Button-->
      <button class="font-light text-sm animate-default hover:text-slate-500">
        Download .CSV
      </button>
      <!--Import CSV Button-->
      <label for="file-input" class="cursor-pointer px-5 font-light text-sm animate-default hover:text-slate-500">
        Import .CSV
      </label>
      <input id="file-input" type="file" accept=".csv" class="hidden" @change="handleFileUpload" />
    </div>
  </div>
</template>

<!---------------------------------------------------------------->

<script>
import default_model from 'raw-loader!../assets/default_model.csv'
import TableComponent from '@/components/visualizers/TableComponent'
import Sidebar from '@/components/containers/Sidebar'
import FilterDropdown from '@/components/containers/FilterDropdown'

import { reactive, inject, computed } from 'vue'

export default {
  name: "ModelTab",
  components: {
    TableComponent,
    Sidebar,
    FilterDropdown
  },

  setup() {
    /**
     * IMPORTS
     */
    const model = inject('model')
    const objectFilter = inject('object filter')
    const objectMap = inject('object map')

    const filters = reactive({
      main: {
        'ID': {
          value: false,
          requires: [],
          description: ''
        },
        'Factors': {
          value: true,
          requires: [],
          description: ''
        },
      },
      sales: {
        'Normal online sales': {
          value: true,
          requires: [],
          description: ''
        },
        'In-store sales': {
          value: true,
          requires: [],
          description: ''
        },
        'Duplicate online sales': {
          value: true,
          requires: [],
          description: ''
        },
      },
      returns: {
        'Purchased online returns': {
          value: true,
          requires: [],
          description: ''
        },
        'Purchased in-store returns': {
          value: true,
          requires: [],
          description: ''
        },
      },
      fraud: {
        'Wardrobing': {
          value: false,
          requires: [],
          description: 'Fraudsters buy an item with the intention of using for an event and returning it after'
        },
        'Payment refund fraud': {
          value: false,
          requires: [],
          description: 'Fraudsters either make the purchase with an illegitimate card, returning merchandise before the amount clears the bank \n OR \n After receiving the parcel, Fraudsters dispute the transaction with their bank directly under false pretences'
        },
        'Multi-channel refund fraud': {
          value: false,
          requires: [],
          description: 'Fraudsters obtain multiple refunds for the same product, by using different channels to claim said refund'
        },
        'Price Arbitrage': {
          value: false,
          requires: [],
          description: 'Fraudsters make an online purchase and return fake or damaged items using the genuine package for a full refund'
        },
        'Shipping fraud': {
          value: false,
          requires: [],
          description: 'Fraudsters request full refund claiming either missing or incomplete parcel'
        },
        'Employee returns fraud': {
          value: false,
          requires: [],
          description: 'Employees use their role to return goods stolen by themselves or acquaintances'
        },
        'Non-Receipt shoplifted fraud': {
          value: false,
          requires: [],
          description: 'Fraudsters return stolen items without a receipt for a store credit refund. They then use said credit or sell it online'
        },
        'Receipt switching fraud': {
          value: true,
          requires: [],
          description: 'Fraudsters make a purchase and leave the store with item, then re-enter later, pick up identical item, and use original receipt to claim refund'
        },
        'Receipt fraud': {
          value: true,
          requires: [],
          description: 'Fraudsters return shoplifted merchandise in-store via found or fake receipt'
        },
      },
      costs: {
        'Absolute Cost per month (£)': {
          value: false,
          requires: [],
          description: ''
        },
        'Absolute Cost per item sold (£)': {
          value: false,
          requires: [],
          description: ''
        },
        'Cost per retail price (%)': {
          value: false,
          requires: [],
          description: ''
        },
        'Absolute Cost per return (£)': {
          value: false,
          requires: [],
          description: ''
        },
        'Potential loss: the % of reduced costs': {
          value: false,
          requires: [],
          description: ''
        }
      }
    })

    const fields = computed(() => {
      var list = []

      for (var field in filters) {
        list = list.concat(objectMap(objectFilter(filters[field], (config) => config.value), (config, key) => {
          list.push({ label: key, field: field })
        }))
      }

      return list.filter((i) => i !== undefined)
    })

    return {
      filters,
      fields,
      model,
    }
  },

  methods: {
    parseFile(file) {
      this.$papa.parse(file, {
        header: true,
        skipEmptyLines: true,
        complete: (results) => {
          this.model = results.data
        }
      })
    },

    handleFileUpload(event) {
      this.parseFile(event.target.files[0])
    }
  },

  mounted() {
    this.parseFile(default_model)
  }
}
</script>