import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import VuePapaParse from 'vue-papa-parse'
import './styles/app.css'

const app = createApp(App)
app.use(router).use(VuePapaParse).mount('#app')

function toggle(state) {
  if (state.open) {
    state.open = false
    setTimeout(() => { state.render = false }, 300)
  } else {
    state.open = true
    state.render = true
  }
}

function copy(object) {
  return JSON.parse(JSON.stringify(object))
}

function objectMap(obj, fn) {
  Object.fromEntries(
    Object.entries(obj).map(
      ([k, v], i) => [k, fn(v, k, i)]
    )
  )
}

function isEmpty(obj) {
  for (var prop in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, prop)) {
      return false;
    }
  }

  return JSON.stringify(obj) === JSON.stringify({});
}

function objectFilter(obj, callback) {
  return Object.fromEntries(Object.entries(obj).
    filter(([key, val]) => callback(val, key)));
}

app.provide('toggle', toggle)
app.provide('copy', copy)
app.provide('object map', objectMap)
app.provide('object filter', objectFilter)
app.provide('is empty', isEmpty)