<template>
  <Bar />
</template>

<script>
import { defineComponent, h, inject, computed, watch, ref } from 'vue'
import { Bar } from 'vue-chartjs'

import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

export default defineComponent({
  name: 'BarChart',

  props: {
    chartId: {
      type: String,
      default: 'bar-chart'
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {
        return {
          height: `300px`,
          width: '500px',
          position: 'relative'
        }
      }
    },
    plugins: {
      type: Array,
      default: () => []
    }
  },

  setup(props) {
    /**
     * IMPORTS
     */
    const retailerType = inject("retailer type")
    const onlineFilters = inject('online filters')
    const instoreFilters = inject('in-store filters')
    const onlineData = inject('online data')
    const instoreData = inject('in-store data')
    const onlineDataAlt = inject('online data alt')
    const instoreDataAlt = inject('in-store data alt')
    const objectFilter = inject('object filter')
    const isEmpty = inject('is empty')

    const compare = !isEmpty(instoreDataAlt.value)

    /**
     * generates the labels for the table, from the current filters
     */
    const filtered = computed(() => {
      var list = []

      const filters = (retailerType.value === 'online') ? onlineFilters : instoreFilters

      for (var k in filters)
        for (var i in objectFilter(filters[k], (config) => config.value))
          list.push(i)

      return list
    })

    const selectedConfigs = computed(() => {
      let x = []
      let configs = (retailerType.value === 'online') ? onlineData.value : instoreData.value

      let configsAlt
      if (compare)
        configsAlt = (retailerType.value === 'online') ? onlineDataAlt.value : instoreDataAlt.value

      for (var i in configs)
        if (filtered.value.includes(i)) {
          configs[i].label = i
          if (compare)
            x.push({ label: configs[i].label, 'initial': configs[i]['initial'], 'post policies (L)': configs[i]['post policies'], 'post policies (R)': configsAlt[i]['post policies'] })
          else
            x.push(configs[i])
        }

      return x
    })

    const labels = computed(() => selectedConfigs.value.map((config) => config.label))

    const datasets = computed(() => {
      const colors = ['#2563eb', '#4ade80', '#dc2626', '#facc15']

      if (compare)
        return [
          {
            label: 'initial',
            backgroundColor: colors[0],
            data: selectedConfigs.value.map((config) => config['initial'])
          },
          {
            label: 'post policies (L)',
            backgroundColor: colors[1],
            data: selectedConfigs.value.map((config) => config['post policies (L)'])
          },
          {
            label: 'post policies (R)',
            backgroundColor: colors[3],
            data: selectedConfigs.value.map((config) => config['post policies (R)'])
          }
        ]
      else
        return [
          {
            label: 'initial',
            backgroundColor: colors[0],
            data: selectedConfigs.value.map((config) => config['initial'])
          },
          {
            label: 'post policies',
            backgroundColor: colors[1],
            data: selectedConfigs.value.map((config) => config['post policies'])
          }
        ]
    })

    const chartData = ref({
      labels: labels.value,
      datasets: datasets.value
    })

    watch(selectedConfigs, () => {
      chartData.value = {
        labels: labels.value,
        datasets: datasets.value
      }
    })

    const chartOptions = {
      responsive: true,
      maintainAspectRatio: false
    }

    return () =>
      h(Bar, {
        chartData: chartData.value,
        chartOptions,
        chartId: props.chartId,
        cssClasses: props.cssClasses,
        styles: props.styles,
        plugins: props.plugins
      })
  },
  components: { Bar }
})

</script>