<template>
  <button @click="toggle(state)" class="z-20 relative shadow-sm rounded-full p-2 flex-none animate-default" :class="state.open
  ? 'bg-yellow-400 text-yellow-50 shadow-md scale-[-130%] -translate-x-4 hover:-translate-x-6'
  : 'bg-yellow-400 text-yellow-50 shadow-md scale-100 hover:-translate-x-2'">
    <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"
      stroke-width="2">
      <path stroke-linecap="round" stroke-linejoin="round"
        :d="state.open ? 'M6 18L18 6M6 6l12 12' : 'M8 7h12m0 0l-4-4m4 4l-4 4m0 6H4m0 0l4 4m-4-4l4-4'" />
    </svg>
  </button>

  <div v-show="state.open" class="z-10 grid place-items-center fixed top-0 left-0 w-full h-full animate-default"
    :class="state.render ? 'bg-black bg-opacity-30' : 'bg-black bg-opacity-0'">
    <results class="w-fit h-fit" :online="valuesOnline" :instore="valuesInstore" :onlineAlt="valuesOnlineAlt"
      :instoreAlt="valuesInstoreAlt" />
  </div>
</template>

<script>
import { inject, reactive, computed } from 'vue'
import Results from '@/components/Results'

export default {
  name: 'CompareVue',
  components: {
    Results
  },

  props: {
    data: Array
  },

  setup(props) {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')

    const state = reactive({
      open: false,
      render: false
    })

    const valuesOnline = computed(() => props.data[0]['online'])
    const valuesInstore = computed(() => props.data[0]['in-store'])

    const valuesOnlineAlt = computed(() => props.data[1]['online'])
    const valuesInstoreAlt = computed(() => props.data[1]['in-store'])

    return {
      state,
      toggle,
      valuesOnline,
      valuesInstore,
      valuesOnlineAlt,
      valuesInstoreAlt
    }
  }
}
</script>