<template>
  <template v-if="text">
    <span class="flex flex-col relative">
      <button id="dropdown-button" @click="toggle(state)"
        class="flex items-center mx-2 pr-1 group shrink appearance-none bg-transparent bg-none text-indigo-600 font-medium outline-none ring-0 animate-default">
        <span class="flex flex-row items-center">
          <rotating-arrow :toggle="state.open" />
          <span class=" border-0 border-b-2 border-indigo-500 group-hover:text-indigo-400 animate-default">
            {{ modelValue }}
          </span>
        </span>
      </button>
      <ul class="animate-default origin-top overflow-hidden" :class="state.open ? 'max-h-[500px]' : 'max-h-[0px]'">
        <li class="font-light" v-for="option in options" :key="option">
          <button v-show="state.render"
            class="p-1 w-full text-left text-indigo-500 font-normal hover:text-indigo-600 hover:tracking-wider focus:text-indigo-600 focus:tracking-wider outline-none ring-0 animate-fast"
            @click="toggle(state); $emit('update:modelValue', option); $emit('change', option)">
            {{ option }}
          </button>
        </li>
      </ul>
    </span>
  </template>

  <template v-else>
    <div class="bg-indigo-200 p-1 shadow-inner border-indigo-900 w-fit h-fit rounded-full">
      <div class="rounded-full w-fit flex flex-row overflow-hidden space-x-1">
        <template v-for="option in options" :key="option">
          <button class="animate-default p-1 text-sm whitespace-pre"
            :class="(modelValue === option) ? 'bg-indigo-600 shadow-lg text-indigo-100 font-bold px-2' : 'hover:px-1 hover:bg-white shadow-md'"
            @click="$emit('update:modelValue', option);">
            {{ option }}
          </button>
        </template>
      </div>
    </div>
  </template>
</template>

<!---------------------------------------------------------------->

<script>
import RotatingArrow from '@/components/miscellaneous/RotatingArrow'

import { inject, reactive } from 'vue'

export default {
  name: "SelectorVue",
  components: { RotatingArrow },

  props: {
    options: Array,
    modelValue: String,
    text: Boolean,
    block: Boolean,
  },

  setup() {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')

    const state = reactive({
      open: false,
      render: false
    })

    return {
      state,
      toggle,
    }
  }
}
</script>