<template>
	<!-- Various global.configs -->
	<form class=" overflow-y-auto h-full scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100"
		@input="validate" v-on:submit.prevent>
		<!--Retailer Information Section-->
		<section-wrapper title="Information">
			<div class="flex place-content-center font-light w-full">
				<div class="space-y-6 py-6">
					<span class="flex flex-row">I am an
						<selector :options="retailerSelect.options" v-model="retailerSelect.type"
							:descript="retailerSelect.description" text />retailer.
					</span>
					<span class="flex flex-row">
						I sell
						<selector :options="productSelect.options" v-model="productSelect.type"
							:description="productSelect.description" text />
						.
					</span>
				</div>
			</div>
		</section-wrapper>
		<!--Initial Values Section-->
		<section-wrapper title="Initial Values">
			<div class="flex flex-wrap gap-2 place-content-center font-bold py-6">
				<div class="text-center max-w-lg min-w-fit flex-1" v-show="retailerType !== 'in-store'">
					Online :
					<div class="border-4 mt-2 border-slate-200 bg-white rounded-lg p-4 w-full h-fit space-y-4 font-normal">
						<!--Online Main Values-->
						<div class="flex flex-col gap-4">
							<div class="flex flex-grow" v-for="(config, label) in valuesOnline.main" :key="config">
								<input-vue class="flex-1" :config="config" :label="label" v-model="config.value" />
							</div>
						</div>
						<!--Online Fraud Breakdown Dropdown-->
						<dropdown title="fraud breakdown">
							<div class="flex flex-col gap-4">
								<input-vue v-for="(config, label) in valuesOnline.fraud" :key="config" :config="config" :label="label"
									v-model="config.value" />
							</div>
						</dropdown>
						<!--Online Costs Dropdown-->
						<dropdown title="costs">
							<div class="flex flex-col gap-4">
								<input-vue v-for="(config, label) in valuesOnline.costs" :key="config" :config="config" :label="label"
									v-model="config.value" />
							</div>
						</dropdown>
					</div>
				</div>
				<div class="text-center max-w-lg min-w-fit flex-1" v-show="retailerType !== 'online'">
					In-store :
					<div class="border-4 mt-2 border-slate-200 bg-white rounded-lg p-4 w-full h-fit space-y-4 font-normal">
						<!--Instore Main Values-->
						<div class="flex flex-col gap-4">
							<div class="flex flex-grow" v-for="(config, label) in valuesInstore.main" :key="config">
								<input-vue class="flex-1" :config="config" :label="label" v-model="config.value" />
							</div>
						</div>
						<!--Instore Fraud Breakdown Dropdown-->
						<dropdown title="fraud breakdown">
							<div class="flex flex-col gap-4">
								<input-vue v-for="(config, label) in valuesInstore.fraud" :key="config" :config="config" :label="label"
									v-model="config.value" />
							</div>
						</dropdown>
						<!--Instore Costs Dropdown-->
						<dropdown title="costs">
							<div class="flex flex-col gap-4">
								<input-vue v-for="(config, label) in valuesInstore.costs" :key="config" :config="config" :label="label"
									v-model="config.value" />
							</div>
						</dropdown>
					</div>
				</div>
			</div>
		</section-wrapper>
	</form>
</template>

<!---------------------------------------------------------------->

<script>
import InputVue from '@/components/inputs/InputVue'
import SectionWrapper from '@/components/containers/SectionWrapper'
import Selector from '@/components/inputs/Selector'
import Dropdown from '@/components/containers/Dropdown'

import { watch, reactive, inject, computed } from 'vue'

export default {
	name: 'ConfigurationTab',
	components: {
		InputVue,
		SectionWrapper,
		Selector,
		Dropdown
	},

	setup() {
		/**
		 * IMPORTS
		 */
		const presetValues = inject('preset values')
		const findConfigByName = inject('find config by name')
		const retailerSelect = inject('retailer select')
		const valuesOnline = inject('values online')
		const valuesInstore = inject('values in-store')

		const productSelect = reactive({
			type: 'women\'s clothing',
			options: [
				'women\'s clothing',
				'men\'s clothing',
				'children\'s clothing',
				'footwear',
				'other',
			],
			description: 'only used as preset values'
		})

		const productType = computed(() => productSelect.type)
		const retailerType = computed(() => retailerSelect.value.type)

		watch(productType, (value) => {
			const presets = presetValues.value[value]

			for (var key in presets)
				findConfigByName(key).value = presets[key]
		})

		return {
			retailerSelect,
			retailerType,
			productSelect,
			valuesOnline,
			valuesInstore
		}
	}
};
</script>