<template>
  <div
    class="bg-white rounded-xl flex flex-col animate-default origin-left flex-none overflow-auto scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100"
    :class="state.open ? 'w-[250px]' : 'w-[0px]'">

    <!--Title-->
    <div v-show="title !== ''" class="flex flex-row items-center">
      <h2 v-show="state.render" class="p-2 font-bold block text-right mb-2 text-indigo-800 animate-fast overflow-hidden"
        :class="state.open ? 'max-w-[250px]' : 'max-w-[0px]'">{{ title }}</h2>
      <div class="grow" v-show="state.render" />
    </div>

    <!--Button-->
    <template v-if="small">
      <button @click="toggle(state)"
        class="absolute shadow-sm rounded-md top-0 z-10 p-1 flex-none mr-0 animate-default origin-left"
        :class="state.open ? 'left-[244px] bg-indigo-600 text-indigo-100 mt-2 shadow-md scale-[-100%]' : 'left-[0px] shadow-xs hover:shadow-md border-2 bg-slate-50 text-slate-600 hover:bg-white hover:text-slate-800'">
        <!--Icon-->
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor"
          stroke-width="2">
          <path stroke-linecap="round" stroke-linejoin="round"
            :d="state.open ? 'M6 18L18 6M6 6l12 12' : 'M4 6h16M4 12h16M4 18h16'" />
        </svg>
      </button>
    </template>
    <template v-else>
      <button @click="toggle(state)"
        class="absolute shadow-sm rounded-full top-0 z-10 p-2 flex-none mr-0 animate-default origin-left"
        :class="state.open ? 'left-[244px] bg-indigo-600 text-indigo-100 mt-2 shadow-md scale-[-100%]' : 'left-[0px] mt-2 ml-4 shadow-xs hover:shadow-md bg-slate-200 bg-opacity-70 text-slate-600 hover:bg-white hover:bg-opacity-100 hover:text-slate-800'">
        <!--Icon-->
        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"
          stroke-width="2">
          <path stroke-linecap="round" stroke-linejoin="round"
            :d="state.open ? 'M6 18L18 6M6 6l12 12' : 'M4 6h16M4 12h16M4 18h16'" />
        </svg>
      </button>
    </template>

    <!--Sidebar Content-->
    <div class="pl-2" v-show="state.render">
      <slot />
    </div>
  </div>
</template>
 
<!---------------------------------------------------------------->

<script>
import { reactive, inject } from 'vue'

export default {
  name: "SidebarVue",

  props: {
    title: { // title of the sidebar
      type: String,
      default: ''
    },
    default: { // whether it is open or close by default
      type: Boolean,
      default: true
    },
    small: Boolean
  },

  setup() {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')

    const state = reactive({
      open: false,
      render: false
    })

    return {
      state,
      toggle
    }
  }
}
</script>