<template>
  <div class="flex flex-col items-start w-full border-b-2 border-indigo-500 bg-indigo-100 rounded-t-lg">

    <!--Button-->
    <button @click="toggle(state)" class="p-1 flex w-full group space-x-2 items-center">
      <!--Rotating Icon-->
      <rotating-arrow :toggle="state.open" />
      <!--Title-->
      <h2 class="text-indigo-600 animate-default group-hover:text-indigo-400 text-lg">
        {{ title }}
      </h2>
    </button>

    <!--Contained Section-->
    <section class="w-full font-light overflow-hidden origin-top animate-default px-2"
      :class="[state.open ? 'max-h-[1000px] py-2' : 'max-h-0']">
      <template v-if="state.render">
        <slot />
      </template>
    </section>
  </div>
</template>

<!---------------------------------------------------------------->

<script>
import RotatingArrow from '@/components/miscellaneous/RotatingArrow'

import { reactive, inject } from 'vue'

export default {
  name: "DropdownVue",
  components: { RotatingArrow },

  props: {
    title: String
  },

  setup() {
    /**
     * IMPORTS
     */
    const toggle = inject('toggle')

    const state = reactive({
      open: false,
      render: false,
    })

    return {
      state,
      toggle
    }
  }
}
</script>