<template>
  <div class="flex flex-row gap-4 w-full h-full relative">
    <sidebar title="filters" small>
      <ul>
        <template v-if="retailerType === 'online'">
          <li v-for="(obj, section) in onlineFilters" :key="section">
            <filter-dropdown :title="section" :filters="obj" />
          </li>
        </template>
        <template v-else>
          <li v-for="(obj, section) in instoreFilters" :key="section">
            <filter-dropdown :title="section" :filters="obj" />
          </li>
        </template>
      </ul>
    </sidebar>
    <bar-chart />
  </div>
</template>

<script>
import { defineComponent, inject, reactive, provide } from 'vue'

import BarChart from '@/components/visualizers/charts/BarChart.vue'
import Sidebar from '@/components/containers/Sidebar.vue'
import FilterDropdown from '@/components/containers/FilterDropdown.vue'

export default defineComponent({
  name: 'BarVue',

  setup() {
    /**
     * IMPORTS
     */
    const retailerType = inject("retailer type")

    function makeFilter(val) {
      return { requires: [], value: val, description: '' }
    }

    // Generates original filters for online values
    var onlineFilters = reactive({
      main: {
        'total sales': makeFilter(true),
        'sales - returns': makeFilter(true),
        'total returns': makeFilter(false),
        'genuine sales': makeFilter(false),
        'duplicate sales': makeFilter(true),
        'genuine returns': makeFilter(true),
        'fraudulent returns': makeFilter(true),
      },
      fraud: {
        'wardrobing': makeFilter(false),
        'price arbitrage': makeFilter(false),
        'payment refund fraud': makeFilter(false),
        'multi-channel refund fraud': makeFilter(false),
        'shipping fraud': makeFilter(false)
      }
    })

    // Generates original filters for in-store values
    var instoreFilters = reactive({
      main: {
        'total sales': makeFilter(true),
        'sales - returns': makeFilter(true),
        'total returns': makeFilter(true),
        'genuine returns': makeFilter(true),
        'fraudulent returns': makeFilter(true),
      },
      fraud: {
        'wardrobing': makeFilter(false),
        'non-receipt shoplifted fraud': makeFilter(false),
        'receipt switching fraud': makeFilter(false),
        'receipt fraud': makeFilter(false),
        'payment refund fraud': makeFilter(false),
        'employee fraud': makeFilter(false),
        'multi-channel refund fraud': makeFilter(false)
      }
    })

    provide('online filters', onlineFilters)
    provide('in-store filters', instoreFilters)

    return {
      onlineFilters,
      instoreFilters,
      retailerType
    }
  },
  components: { BarChart, Sidebar, FilterDropdown }
})

</script>