<template>
  <!--When active-->
  <span v-if="active">
    <!--Tick Box-->
    <input :id="uuid"
      class="form-check-input appearance-none h-4 w-4 mr-2 border border-slate-300 rounded-sm bg-white checked:bg-indigo-600 checked:border-indigo-600 focus:outline-none mt-1 align-top float-left cursor-pointer animate-fade"
      :class="filter && 'rounded-full h-3 w-3 mr-0'" type="checkbox" :value="modelValue"
      @change="$emit('update:modelValue', $event.target.checked)" :checked="modelValue">
    <!--Text + Info Icon-->
    <label class="form-check-label align-top" :class="filter && 'text-sm font-light'" :for="uuid">
      {{ label }}
      <tooltip :label="annotation.value" />
    </label>
  </span>

  <!--When unactive-->
  <span v-else class="cursor-not-allowed" :title="annotation">
    <!--Tick Box-->
    <input disabled
      class="form-check-input appearance-none h-4 w-4 mr-2 border border-slate-300 rounded-sm bg-slate-100 mt-1 align-top float-left animate-fade cursor-not-allowed"
      type="checkbox" :value="modelValue">
    <!--Text + Question Mark-->
    <label class="form-check-label align-top text-slate-400 cursor-not-allowed">
      {{ label }}
      <span class="ml-1 text-xs align-super cursor-not-allowed"> ?</span>
    </label>
  </span>
</template>

<!---------------------------------------------------------------->

<script>
import Tooltip from '@/components/miscellaneous/Tooltip'
import { v4 as uuidv4 } from 'uuid'

import { inject, computed, watch } from 'vue'

export default {
  name: 'CheckboxVue',
  components: { Tooltip, },

  props: {
    label: String,
    config: Object,
    modelValue: Boolean,
    deactivated: Boolean,
    filter: Boolean,
    local: Object
  },

  setup(props, { emit }) {
    /**
     * IMPORTS
     */
    const dependency = inject('dependency')
    const localDependency = inject('local dependency')

    const uuid = uuidv4()

    const description = computed(() => props.config.description)
    const requirement = computed(() => props.local === undefined ? dependency(props.config.requires) : localDependency(props.config.requires, props.local))
    const modelled = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })

    const active = computed(() => props.deactivated ? false : requirement.value.value)

    const annotation = computed(() => requirement.value.value ? description : 'requires "' + requirement.value.label + '" to be ' + requirement.value.to_be)

    watch(requirement, (obj) => {
      if (!obj.value.value) modelled.value = false
    })

    return {
      uuid,
      annotation,
      active,
    }
  }
}
</script>