<template>
  <div class="flex flex-row relative">
    <sidebar title="filters" small>
      <ul>
        <template v-if="retailerType === 'online'">
          <li v-for="(obj, section) in onlineFilters" :key="section">
            <filter-dropdown :title="section" :filters="obj" />
          </li>
        </template>
        <template v-else>
          <li v-for="(obj, section) in instoreFilters" :key="section">
            <filter-dropdown :title="section" :filters="obj" />
          </li>
        </template>
      </ul>
    </sidebar>
    <!--Table with results-->
    <table class="divide-y divide-slate-400">
      <thead class="bg-opacity-0">
        <tr>
          <template v-for="field in labels" :key='field'>
            <th v-if="field !== ' '" scope="col" class="px-3 py-1 text-sm">
              {{  field  }}
            </th>
            <th v-else class="font-light italic text-sm">
              {{  retailerType  }}
            </th>
          </template>
        </tr>
      </thead>
      <tbody class="divide-y divide-indigo-100">
        <tr v-for="item in data" :key="item">
          <td v-for=" field in labels" :key="field" class="px-2 py-1 text-center"
            :class="[themes[item[' ']], themes[field]]">
            {{  item[field]  }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { reactive, computed, inject } from 'vue'
import Sidebar from '@/components/containers/Sidebar.vue'
import FilterDropdown from '@/components/containers/FilterDropdown.vue'

export default {
  name: 'ResultsTable',

  setup() {
    /**
     * IMPORTS
     */
    const retailerType = inject('retailer type')
    const onlineData = inject('online data')
    const instoreData = inject('in-store data')
    const onlineDataAlt = inject('online data alt')
    const instoreDataAlt = inject('in-store data alt')
    const objectFilter = inject('object filter')
    const isEmpty = inject('is empty')

    const compare = !isEmpty(onlineDataAlt.value)

    const themes = reactive({
      'initial': 'bg-white bg-opacity-50',
      'post policies': 'bg-white bg-opacity-50',
      ' ': 'font-bold text-start text-sm',
    })

    function makeFilter(val) {
      return { requires: [], value: val, description: '' }
    }

    // Generates original filters for online values
    var onlineFilters = reactive({
      main: {
        'total sales': makeFilter(true),
        'sales - returns': makeFilter(true),
        'total returns': makeFilter(false),
        'genuine sales': makeFilter(false),
        'duplicate sales': makeFilter(true),
        'genuine returns': makeFilter(true),
        'fraudulent returns': makeFilter(true),
      },
      fraud: {
        'wardrobing': makeFilter(false),
        'price arbitrage': makeFilter(false),
        'payment refund fraud': makeFilter(false),
        'multi-channel refund fraud': makeFilter(false),
        'shipping fraud': makeFilter(false)
      }
    })

    // Generates original filters for in-store values
    var instoreFilters = reactive({
      main: {
        'total sales': makeFilter(true),
        'sales - returns': makeFilter(true),
        'total returns': makeFilter(true),
        'genuine returns': makeFilter(true),
        'fraudulent returns': makeFilter(true),
      },
      fraud: {
        'wardrobing': makeFilter(false),
        'non-receipt shoplifted fraud': makeFilter(false),
        'receipt switching fraud': makeFilter(false),
        'receipt fraud': makeFilter(false),
        'payment refund fraud': makeFilter(false),
        'employee fraud': makeFilter(false),
        'multi-channel refund fraud': makeFilter(false)
      }
    })

    /**
     * generates the labels for the table, from the current filters
     */
    const labels = computed(() => {
      var list = [" "]

      const filters = (retailerType.value === 'online') ? onlineFilters : instoreFilters

      for (var k in filters)
        for (var i in objectFilter(filters[k], (config) => config.value))
          list.push(i)

      return list
    })

    const selectedConfigs = computed(() => {
      let x = []
      let configs = (retailerType.value === 'online') ? onlineData.value : instoreData.value

      let configsAlt
      if (compare)
        configsAlt = (retailerType.value === 'online') ? onlineDataAlt.value : instoreDataAlt.value

      for (var i in configs)
        if (labels.value.includes(i)) {
          configs[i].label = i
          if (compare)
            x.push({ label: configs[i].label, 'initial': configs[i]['initial'], 'post policies (L)': configs[i]['post policies'], 'post policies (R)': configsAlt[i]['post policies'] })
          else
            x.push(configs[i])
        }

      return x
    })

    const data = computed(() => {
      var rows

      var data = {}

      if (compare) {
        rows = [{ ' ': 'initial' }, { ' ': 'post policies (L)' }, { ' ': 'post policies (R)' }]

        for (var j in selectedConfigs.value) {
          data[selectedConfigs.value[j].label] = {
            'initial': +parseFloat(selectedConfigs.value[j]['initial']).toFixed(2),
            'post policies (L)': +parseFloat(selectedConfigs.value[j]['post policies (L)']).toFixed(2),
            'post policies (R)': +parseFloat(selectedConfigs.value[j]['post policies (R)']).toFixed(2)
          }
        }
      }
      else {
        rows = [{ ' ': 'initial' }, { ' ': 'post policies' }, { ' ': 'difference' }]

        for (var u in selectedConfigs.value) {
          data[selectedConfigs.value[u].label] = {
            'initial': +parseFloat(selectedConfigs.value[u]['initial']).toFixed(2),
            'post policies': +parseFloat(selectedConfigs.value[u]['post policies']).toFixed(2),
            'difference': +parseFloat(selectedConfigs.value[u]['post policies'] - selectedConfigs.value[u]['initial']).toFixed(2)
          }
          data[selectedConfigs.value[u].label]['difference'] = data[selectedConfigs.value[u].label]['difference'] + ' / ' + +parseFloat(data[selectedConfigs.value[u].label]['difference'] / selectedConfigs.value[u]['initial'] * 100).toFixed(2) + '%'
        }
      }

      for (var l in data) {
        rows.map((row) => {
          row[l] = data[l][row[' ']]
        })
      }

      return rows
    })

    return {
      themes,
      labels,
      data,
      retailerType,
      onlineFilters,
      instoreFilters
    }
  },
  components: { Sidebar, FilterDropdown }
}
</script>