<template>
  <table
    class="table-auto border-none text-center w-full h-full scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100">
    <thead class="sticky top-0 shadow border-slate-500 bg-slate-100">
      <tr>
        <th scope="col" v-for="field in fields" :key='field' class="p-1 px-2"
          :class="(field.field !== 'main') && 'font-medium'">
          {{  field.label  }}
        </th>
      </tr>
    </thead>
    <tbody class="overflow-auto scrollbar scrollbar-thin scrollbar-thumb-slate-300 scrollbar-track-slate-100">
      <tr v-for="item in data" :key='item'>
        <td v-for="field in fields" :key='field' class="border-b"
          :class="[theme[field.field], theme[item[field.label]]]">
          <span>{{  item[field.label]  }}</span>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<!---------------------------------------------------------------->

<script>
import { watch } from 'vue'

export default {
  name: "TableComponent",

  props: {
    fields: Array,
    data: Array,
  },

  setup(props) {
    const theme = {
      main: 'font-light text-start',
      sales: 'bg-green-100 border-green-200',
      returns: 'bg-yellow-100 border-yellow-200',
      fraud: 'bg-orange-100 border-orange-200',
      costs: 'bg-blue-100 border-blue-200',

      'N/A': 'text-red-400 font-thin',
      MISSING: 'font-light',
      UNSURE: 'font-light'
    }

    watch(props.data, () => console.log('yes'))

    return {
      theme
    }
  }
}
</script>